export class Api {
  constructor(dataApi) {
    this._authorization = dataApi.authorization;
  }

  //загружаем информацию о юзере с сервера
  getUserInfo() {
    return fetch("https://nomoreparties.co/v1/cohort-64/users/me/", {
      headers: {
        authorization: this._authorization,
      },
    }).then((res) => {
      if (res.ok) {
        return res.json();
      }
      return Promise.reject(`Что-то сломалось. Ошибка: ${res.status}`);
    });
  }
  //загружаем карточки с сервера
  getInitialCards() {
    return fetch("https://mesto.nomoreparties.co/v1/cohort-64/cards/", {
      headers: {
        authorization: this._authorization,
      },
    }).then((res) => {
      if (res.ok) {
        return res.json();
      }
      return Promise.reject(`Что-то сломалось. Ошибка: ${res.status}`);
    });
  }
  // отправляем данные юзера на сервер
  patchUserInfo(data) {
    return fetch("https://nomoreparties.co/v1/cohort-64/users/me/", {
      method: "PATCH",
      headers: {
        authorization: this._authorization,
        "Content-Type": "application/json",
      },
      body: JSON.stringify({
        name: data.name,
        about: data.career,
      }),
    }).then((res) => {
      if (res.ok) {
        return res.json();
      }
      return Promise.reject(`Что-то сломалось. Ошибка: ${res.status}`);
    });
  }
  // отправляем данные карточки на сервер
  postNewCard(item) {
    return fetch("https://nomoreparties.co/v1/cohort-64/cards/", {
      method: "POST",
      headers: {
        authorization: this._authorization,
        "Content-Type": "application/json",
      },
      body: JSON.stringify({
        name: item.name,
        link: item.link,
      }),
    }).then((res) => {
      if (res.ok) {
        return res.json();
      }
      return Promise.reject(`Что-то сломалось. Ошибка: ${res.status}`);
    });
  }

  //удалени карточки
  deleteCard(cardId) {
    return fetch(
      `https://mesto.nomoreparties.co/v1/cohort-64/cards/${cardId}/`,
      {
        method: "DELETE",
        headers: {
          authorization: this._authorization,
        },
      }
    ).then((res) => {
      if (res.ok) {
        return res.json();
      }
      return Promise.reject(`Что-то сломалось. Ошибка: ${res.status}`);
    });
  }
  // лайк и дизлайк
  likeCard(cardId) {
    return fetch(
      `https://mesto.nomoreparties.co/v1/cohort-64/cards/${cardId}/likes`,
      {
        method: "PUT",
        headers: {
          authorization: this._authorization,
        },
      }
    ).then((res) => {
      if (res.ok) {
        return res.json();
      }
      return Promise.reject(`Что-то сломалось. Ошибка: ${res.status}`);
    });
  }

  dislikeCard(cardId) {
    return fetch(
      `https://mesto.nomoreparties.co/v1/cohort-64/cards/${cardId}/likes`,
      {
        method: "DELETE",
        headers: {
          authorization: this._authorization,
        },
      }
    ).then((res) => {
      if (res.ok) {
        return res.json();
      }
      return Promise.reject(`Что-то сломалось. Ошибка: ${res.status}`);
    });
  }
}
